.main_header { 
  width: 100vw;
  height: calc(100vh - 3rem);
  display: grid;
  place-items: center;
  margin-top: 3rem;
}

.main_header-container { 
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  gap: 6rem;
}

.main_header-left { 
  margin-top: -3rem;
}

.main_header-left h4 { 
  margin-bottom: 1rem;
  color: var(--color-secondary);
  font-weight: 400;
}

.main_header-left p { 
  margin: 1rem 0 3.5rem;
  font-size: 1.1rem;
}

.main_header-right { 
  display: grid;
  place-items: center;
  position: relative;
}
